#ratings {
  z-index: 200;
  background-color: #140023;
  grid-template-rows: 100%;
  grid-template-columns: 50% 50%;
  display: none;
  position: fixed;
  inset: 9% 0;
}

#ratings h1 {
  color: #fff;
  padding-left: 10px;
  font-family: Oswald;
  font-size: 10vh;
}

#ratings p {
  color: #fff;
  text-overflow: ellipsis;
  white-space: nowrap;
  margin-top: auto;
  margin-bottom: auto;
  padding-left: 10px;
  font-family: Oswald;
  font-size: 3vh;
  overflow: hidden;
}

#ratings #comments {
  flex-direction: column;
  grid-column: 2;
  justify-content: center;
  display: flex;
}

#ratings .vote {
  grid-column: 1;
  grid-row: span;
  justify-content: center;
  align-items: center;
  display: flex;
}
